import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import { GOrderCount, OrderStateCategoryEnum } from '../../../graph/types'
import { QueryOrderCount, SubscribeOrderCount } from './OrderCountWatch.graphql'

interface IProps {
  categories: OrderStateCategoryEnum[]
  render(count: Maybe<number>): ReactNode
}

export const OrderCountWatch: React.SFC<IProps> = ({ render, categories }) => (
  <WithRoot
    render={root => {
      if (!root.order.list.hasBranchFilter) {
        return render(null)
      }
      const variables: GOrderCount.Variables = {
        branchesId: root.order.list.branchesFilter,
        categories,
      }
      return (
        <QueryOrderCount
          variables={variables}
          refetchOnConnectionLost={true}
          renderLoading={false}
          render={({ ordersCount }, result) => (
            <SubscribeOrderCount
              variables={variables}
              queryResult={result}
              handleUpdate={update => ({
                ordersCount: update.updatedOrdersCount.ordersCount,
              })}
            >
              {render(ordersCount)}
            </SubscribeOrderCount>
          )}
        />
      )
    }}
  />
)
