import { RingVolume } from '@material-ui/icons'
import { LocationDescriptor } from 'history'
import React from 'react'
import { Link } from 'react-router-dom'

import { TouchButtonBadge } from '../../../atoms/TouchButtonBadge'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { OrderCallWatch } from '../organisms/OrderCallWatch'

interface INewOrderButtonProps {
  showOnlyText?: boolean
}

export const CreateOrderButton: React.SFC<INewOrderButtonProps> = ({
  showOnlyText = false,
}) => (
  <OrderCallWatch
    render={call => {
      const location: LocationDescriptor = {
        pathname: `/order/create/${call ? call.phone : ''}`,
        state: {
          eventId: call ? call.eventId : null,
        },
      }
      return (
        <Link to={location}>
          <TouchButton
            text={i18n.t`Create order`}
            iconName={showOnlyText ? undefined : 'add_circle'}
            color={Colors.turquoise}
            badge={
              call && call.isRinging ? (
                <TouchButtonBadge>
                  <RingVolume />
                </TouchButtonBadge>
              ) : null
            }
          />
        </Link>
      )
    }}
  />
)
