import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { buildSubscription } from '../../../graph/buildSubscription'
import { GOrderCount, SOrderCount } from '../../../graph/types'

const OrderCountQuery = gql`
  query GOrderCount(
    $branchesId: [ID!]!
    $categories: [OrderStateCategoryEnum!]!
  ) {
    ordersCount(companyBranches: $branchesId, orderStateCategories: $categories)
  }
`

const OrderCountSubscription = gql`
  subscription SOrderCount(
    $branchesId: [ID!]!
    $categories: [OrderStateCategoryEnum!]!
  ) {
    updatedOrdersCount(companyBranches: $branchesId) {
      ordersCount(
        companyBranches: $branchesId
        orderStateCategories: $categories
      )
    }
  }
`

export const QueryOrderCount = buildQuery<
  GOrderCount.Query,
  GOrderCount.Variables
>(OrderCountQuery)

export const SubscribeOrderCount = buildSubscription<
  SOrderCount.Subscription,
  SOrderCount.Variables,
  GOrderCount.Query
>(OrderCountSubscription)
