import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { CanQuery } from '../../../atoms/CanQuery'
import { OrderStateCategoryEnum } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { PageLayout } from '../../../molecules/PageLayout'
import { CallMeButton } from '../molecules/CallMeButton'
import { CreateOrderButton } from '../molecules/CreateOrderButton'
import { MapViewButton } from '../molecules/MapViewButton'
import { OrderButton } from '../molecules/OrderButton'
import { OverviewButton } from '../molecules/OverviewButton'
import { SettingsButton } from '../molecules/SettingsButton'
import { SupportButton } from '../molecules/SupportButton'
import { OrderCountWatch } from '../organisms/OrderCountWatch'

interface IDashboardPageProps {}

function getLayout() {
  return [
    { i: 'b1', x: 0, y: 1, w: 2, h: 2, static: true },
    { i: 'b2', x: 2, y: 1, w: 2, h: 2, static: true },
    { i: 'b3', x: 4, y: 1, w: 2, h: 2, static: true },
    { i: 'b4', x: 10, y: 1, w: 1, h: 2, static: true },
    { i: 'bb1', x: 0, y: 7, w: 2, h: 2, static: true },
    { i: 'bb2', x: 2, y: 7, w: 2, h: 2, static: true },
    { i: 'bb3', x: 4, y: 7, w: 2, h: 2, static: true },
    { i: 'bb4', x: 14, y: 7, w: 2, h: 2, static: true },
  ]
}

export const MainDashboardPage: React.SFC<IDashboardPageProps> = props => (
  <>
    <PageLayout
      layouts={getLayout()}
      pageTitle={i18n.t`Main menu`}
      showSoundControl={true}
    >
      <div key="b1">
        <CanQuery queryName="orders">
          <OrderCountWatch
            categories={[OrderStateCategoryEnum.NEW_ORDERS]}
            render={count => <OrderButton count={count} />}
          />
        </CanQuery>
      </div>
      <div key="b2">
        <CanQuery queryName="orders">
          <MapViewButton />
        </CanQuery>
      </div>
      <div key="b3">
        <CanMutate mutationName="createOrder">
          <CreateOrderButton />
        </CanMutate>
      </div>
      <div key="b4">
        <CanMutate mutationName="createOrder">
          <CallMeButton />
        </CanMutate>
      </div>
      <div key="bb1">
        <SettingsButton />
      </div>
      <div key="bb2">
        <OverviewButton />
      </div>
      <div key="bb4">
        <SupportButton />
      </div>
    </PageLayout>
  </>
)

export default MainDashboardPage
