import React from 'react'
import { Subscription } from 'react-apollo'

import { SOrderCall } from '../../../graph/types'
import { OrderCallSubscription } from './OrderCallWatch.graphql'

interface IProps {
  render(call: Maybe<SOrderCall.UpdatedUserCallingCustomer>): ReactNode
}

export const OrderCallWatch: React.SFC<IProps> = ({ render }) => (
  <Subscription<SOrderCall.Subscription, SOrderCall.Variables>
    subscription={OrderCallSubscription}
  >
    {({ data }) => {
      if (!data) {
        return render(null)
      }
      return render(data.updatedUserCallingCustomer)
    }}
  </Subscription>
)
