import React from 'react'
import { Link } from 'react-router-dom'

import { TouchButtonBadge } from '../../../atoms/TouchButtonBadge'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {
  count: Maybe<number>
}

export const OrderButton: React.SFC<IProps> = ({ count }) => (
  <Link to="/order">
    <TouchButton
      text={i18n.t`Orders`}
      iconName="assignment_returned"
      badge={
        count && count > 0 ? <TouchButtonBadge>{count}</TouchButtonBadge> : null
      }
      color={Colors.yellow}
    />
  </Link>
)
