import React, { useCallback } from 'react'

import useRouterContext from '../../../hooks/useRouterContext'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {}

export const CallMeButton: React.SFC<IProps> = props => {
  if (process.env.REACT_APP_STAGE === 'prod') {
    return null
  }
  const { history } = useRouterContext()
  const makeCall = useCallback((ev: React.MouseEvent<HTMLButtonElement>) => {
    const phone = window.prompt('Zadej telefonní číslo, které ti bude volat')
    if (phone === null) {
      return
    }
    let eventId: MaybeID = null
    if (ev.shiftKey) {
      eventId = window.prompt('A teď ještě eventId, jestli víš co děláš ;)')
    }
    history.push({
      pathname: `/order/create/${phone}`,
      state: {
        eventId,
      },
    })
  }, [])
  return (
    <TouchButton
      text="Zavolej mi [DEV]"
      iconName="ring_volume"
      color={Colors.turquoise}
      onClick={makeCall}
    />
  )
}
