import { DocumentNode } from 'graphql'
import { QueryResult } from 'react-apollo'

import { useEffectOnce } from '../hooks/useEffectOnce'

export function buildSubscription<TSubscription, TVariables, TQuery = {}>(
  subscription: DocumentNode,
) {
  type TResult = QueryResult<TQuery, TVariables>
  interface IProps {
    children: ReactNode
    queryResult: TResult
    variables: TVariables
    handleUpdate(updatedData: TSubscription, prevData: TQuery): TQuery | void
  }
  function GSubscription(props: IProps) {
    useEffectOnce(() => {
      const { queryResult, variables, handleUpdate } = props
      return queryResult.subscribeToMore({
        document: subscription,
        variables,
        updateQuery: (prev, { subscriptionData }) => {
          if (!(subscriptionData && subscriptionData.data)) {
            return prev
          }
          // @ts-ignore Bad typing of updateQuery using TQuery
          return handleUpdate(subscriptionData.data, prev) || prev
        },
      })
    })
    return props.children
  }
  return GSubscription
}
